<template>
  <div class="goods">
    <div ref="menuWarpper" class="menu-wrapper">
      <ul>
        <li class="menu-item" v-for="item in datas.goods">
          <span class="text"><span v-show="item.type > 0" class="iconMap" :class="icon[item.type]"></span>{{item.name}}</span>
        </li>
      </ul>
    </div>
    <div ref="foodsWarapper" class="foods-wrapper">
      <ul>
        <li v-for="item in datas.goods" class="food-list food-list-hook">
          <h1 class="title">{{item.name}}</h1>
          <ul>
            <li v-for="food in item.foods" @click="detailPage(food)" class="food-item">
              <div class="icon"><img :src="food.icon" width="57" height="57" alt=""></div>
              <div class="content">
                <h2 class="name">{{food.name}}</h2>
                <p class="description">{{food.description}}</p>
                <div class="sell-info">
                  <span class="sellCount">月售{{food.sellCount}}份</span>
                  <span class="rating">好评率{{food.rating}}%</span>
                </div>
                <div class="price">
                  <span class="newPrice"><span class="unit">&yen;</span>{{food.price}}</span>
                </div>
                <div class="cartcontrol-wrapper">
                  <cartControl :food="food"></cartControl>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <shopCart :food="food" :deliveryPrice="datas.seller.deliveryPrice" :minPrice="datas.seller.minPrice"></shopCart>
    <foodDetail :foodDetails="foodDetails" ref="foodDetail"></foodDetail>
  </div>
</template>

<script>
  import icon from './mods/icon'
  import Scroll from 'better-scroll'
  import shopCart from './mods/shopCart.vue'
  import cartControl from './mods/cartcontrol.vue'
  import foodDetail from './mods/foodDetail.vue'
  export default {
    props: ['datas'],
    data () {
      return {
        icon: icon,
        foodDetails: {}
      }
    },
    computed: {
      food () {
        let arr = []
        // 回调里的参数   1.value 2.index
        this.datas.goods.forEach(goods => {
          goods.foods.forEach(foods => {
            if (foods.count > 0) {
              arr.push(foods)
            }
          })
        })
        return arr
      }
    },
    mounted () {
      // dom更新完之后 = = = window.onload
      // dom更新完之后 =》 new Scroll() =》 ajax获取到了数据 =》 dom
      this.$nextTick(() => {
      })
    },
    methods: {
      _scroll () {
        /* eslint-disable no-new */
        new Scroll(this.$refs['foodsWarapper'], {
          click: true
        })
        new Scroll(this.$refs['menuWarpper'], {
          click: true
        })
      },
      detailPage (value) {
        this.foodDetails = value
        this.$refs['foodDetail'].foodshow()
      }
    },
    components: {
      shopCart,
      cartControl,
      foodDetail
    }
  }
</script>



